.App {
  text-align: center;
 
}

#root {
  
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main_div {
  margin: 5%;
  height: 500px;
  width: 500px;
  background-color: rgb(135, 116, 187);
  display: flex;
  justify-content: center;
  flex-direction: row;
  border-radius: 15%;
  box-shadow:  0px 0px 250px 250px rgb(168, 161, 190);
  
 }
.todo_style {
  list-style-type: none;
}

.button {
  border-radius: 40%;
}

h1 {
  text-align: center;
}